@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1{font-family:'Open Sans Condensed', sans-serif}*{box-sizing:border-box}.container{max-width:1140px;width:100%;margin:0 auto;padding:0 15px}.container-fluid{width:100%;padding:0 15px}.row{margin:0 -15px}.character-body{display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:auto auto auto auto;grid-gap:30px 30px;font-size:14px}.character-body-col{width:100%}.character-body-col-group{display:flex;width:100%;align-items:flex-start}.character-body-stats-col{margin-left:15px;width:100%}.form-input{display:block;border:none;width:100%;background-color:transparent}.form-input-label{display:block}.form-row{display:flex}.check-group{border:2px solid black;width:100%;padding-top:5px}.check-input{display:flex;position:relative;padding-left:5px;margin-bottom:2px;cursor:pointer;font-size:14px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.check-input__checkbox-icon{display:inline-block;border:1px solid #505050;border-radius:50%;width:10px;height:10px}.check-input__checkbox-icon::after{content:'';display:none;width:10px;height:10px;display:block;background-color:#505050}.check-input__input-wrap{width:25px;margin:0 5px}.check-input__input-wrap::after{content:'';display:block;width:25px;height:1px;background-color:black}.check-input__input{display:block;width:100%;border:none;text-align:center}.check-input__input:focus{outline:none}.check-group__text{display:block;text-align:center;font-weight:bold;line-height:1;margin:10px 0}body{background-color:#f7f7f7}body *{color:#505050;box-sizing:border-box}.centered{display:block;text-align:center}.header{margin-bottom:30px}

.character-header{display:flex;width:100%;margin-bottom:30px}.character-header__name{width:40%;min-height:170px;background-image:url(/dnd-character/static/media/headerChar.ae7636d5.svg);background-repeat:no-repeat;background-size:contain;padding:100px 40px 0}.character-header__name label{display:block;margin-top:50px;text-transform:uppercase}.character-header__info{width:60%;min-height:190px;background-image:url(/dnd-character/static/media/headerInfo.839de546.svg);background-repeat:no-repeat;background-size:contain;background-position:100% 90%;-webkit-transform:translateX(-4px);transform:translateX(-4px);padding:60px 40px 20px 40px}.character-header__info-row:first-of-type{margin-bottom:15px}

.character-stats{max-width:125px;width:100%;background-color:#efefef;padding:25px 15px}.character-stats__item{background-image:url(/dnd-character/static/media/stat.6b70027d.svg);background-repeat:no-repeat;background-size:cover;display:flex;flex-direction:column;width:100%;min-height:110px;text-align:center;padding:8px 0 6px 0;margin-bottom:15px}.character-stats__item-label{font-weight:bold}.character-stats__item-input{display:block;max-width:48px;width:100%;margin:auto auto 0;border:none;background-color:transparent;text-align:center;padding:8px 0 0;border-radius:50%;font-size:20px;font-weight:bold;-webkit-transform:translateX(1px);transform:translateX(1px)}.character-stats__item-input:focus{outline:none}

.character-inspiration{width:100%;display:flex;min-height:40px;margin-bottom:15px}.character-inspiration-btn{display:flex;width:40px;height:100%}.character-inspiration-btn svg{align-self:center}.character-inspiration-btn:hover{cursor:pointer}.character-inspiration__body{display:flex;align-self:center;width:100%;max-width:146px;min-height:43px;background-image:url(/dnd-character/static/media/inspiration.b6de3187.svg);background-repeat:no-repeat;background-size:contain;padding:0 15px 0 10px;text-align:center}.character-inspiration__body span{align-self:center;display:block;width:100%}

.character-proficiency{width:100%;display:flex;min-height:70px}.character-proficiency__head{background-repeat:no-repeat;background-size:cover;max-width:45px}.character-proficiency-btn{display:flex;width:40px;height:100%}.character-proficiency-btn svg{align-self:center}.character-proficiency-btn:hover{cursor:pointer}.character-proficiency__body{display:flex;align-self:center;width:100%;min-height:43px;max-width:146px;background-repeat:no-repeat;background-size:contain;padding:0 15px 0 10px;text-align:center}.character-proficiency__body span{align-self:center;display:block;width:100%}

.character-saving-throws{background-repeat:no-repeat;background-size:contain;min-height:160px;margin-bottom:15px}

